.gh-content {
  position: relative;
}

.gh-toc > .toc-list {
  position: relative;
}

.toc-list {
  overflow: hidden;
  list-style: none;
}

@media (min-width: 1500px) {
  /* grid-template-columns for when the browser window is >= 1500px */
  .gh-sidebar {
    position: absolute;
    top: 0;
    bottom: 0;
    margin-top: 4vmin;
    grid-column: wide-start / main-start; 
  }

  .gh-sticky-sidebar {
    display: flex;
    flex-direction: column;
    max-height: calc(100vh - 80px);
    position: -webkit-sticky;
    position: sticky;
    top: 50px;
  }

  .gh-toc {
    align-self: stretch;
    overflow-y: auto;
    padding: 1px 0 6vmin;
  }
}

.gh-toc .is-active-link::before {
  background-color: var(
    --ghost-accent-color
  ); /* Defines TOC   accent color based on Accent color set in Ghost Admin */
}
